.lc-input-container{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding-top: 15px;
	.input-text-label{
		width: calc(50% - 25px);
		margin-bottom: 40px;
		input{
			width: 100%;
			padding: 10px 15px 15px;
			border: 0;
			border-bottom: 1px solid #3A3A3A;
		}
		.ui-datepicker-trigger{
			position: absolute;
			right: 20px;
			bottom: 15px;
			cursor: pointer;
		}
	}
}

.sbmt--pen{
	padding: 12px 30px;
	box-shadow: none;
	border: 2px solid #EF4949;
	border-radius: 5px;
	color: #EF4949;
	background-color: transparent;
	transition: .3s;
	cursor: pointer;
	font-family: "montserat";
	padding-left: 50px;
	background-image: url(../images/svg/pen-red.svg);
	background-position: left 10px center;
	background-repeat: no-repeat;
	&:hover{
		background-color: #EF4949;
		color: #fff;
		background-image: url(../images/svg/pen-w.svg);
	}
}